import ModalCommon from "../../common/Modal/Modal";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import CommonBtn from "../../common/CommonBtn";
import "./styles.css";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  ProductPut,
  ProductUpload,
  ProductUpload1,
  ProductUpload2,
} from "../../../redux/products";
import SelectCommon from "../../common/select/index";
import DraverCommon from "../../common/Drawer";
import { Spin, Image } from "antd";
import InputCommon from "../../common/input";
import { LoadingOutlined } from "@ant-design/icons";
import TextArea from "antd/es/input/TextArea";
import Cookies from "universal-cookie";
const cookies = new Cookies();

const antIcon = (
  <LoadingOutlined
    style={{
      fontSize: 24,
      color: "#000",
    }}
    spin
  />
);

function ProductsPut({ openPut, handleClosePut, put_id }) {
  const dispatch = useDispatch();

  const id = put_id;

  const Data = useSelector((state) => state.product.productsGet.data);

  const FilterData = Data.filter((elem) => elem.id == id);

  const uploadDatas = useSelector((state) => state.product.productUpload);
  const uploadDatas1 = useSelector((state) => state.product.productUpload1);
  const uploadDatas2 = useSelector((state) => state.product.productUpload2);

  const HandleChange = (e) => {
    dispatch(ProductUpload(e));
  };
  const HandleChange1 = (e) => {
    dispatch(ProductUpload1(e));
  };
  const HandleChange2 = (e) => {
    dispatch(ProductUpload2(e));
  };

  // states

  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const [nameUz, setNameUz] = useState();

  const [descriptionRu, setDescriptionRu] = useState();
  const [descriptionEn, setDescriptionEn] = useState();
  const [descriptionUz, setDescriptionUz] = useState();

  const [price, setPrice] = useState();
  const [optimalPrice, setOptimalPrice] = useState();
  const [color, setColor] = useState();

  const [count, setCount] = useState();
  const [garante, setGarante] = useState();
  const [max, setMax] = useState();

  const [size, setSize] = useState();
  const [priceDis, setPriceDis] = useState();
  const [link, setLink] = useState();

  const [three, setThree] = useState();
  const [six, setSix] = useState();
  const [tweleve, setTweleve] = useState();

  const [category, setCategory] = useState();
  const [subCategory, setSubCategory] = useState();
  const [brand, setBrand] = useState();
  const [disabled, setDisabled] = useState(true);

  // select options

  const CategoryOptions = [];
  const CategoryDatas = useSelector(
    (state) => state.category.categoriesGet.data
  );
  CategoryDatas?.map((elem) =>
    CategoryOptions.push({
      label: elem.category_ru,
      value: elem.id,
    })
  );

  const HandelCategoryChange = (e) => {
    setCategory(e);
    setDisabled(false);
  };

  // sub

  const SubCategoryOptions = [];

  CategoryDatas.map((elem) =>
    elem.sub_category.map((items) =>
      SubCategoryOptions.push({
        label: items.title_ru,
        value: items.id,
      })
    )
  );

  const HandelSubCategoryChange = (e) => {
    setSubCategory(e);
  };

  const BrandOptions = [];
  const BrandDatas = useSelector((state) => state.brand.brandsGet.data);
  BrandDatas.map((elem) =>
    BrandOptions.push({
      label: elem.brands_name_ru,
      value: elem.id,
    })
  );

  const HandelBrandChange = (e) => {
    setBrand(e);
  };

  const HandelSubmit = async (e) => {
    e.preventDefault();
    const body = {
      product_name_uz: nameUz,
      product_name_en: nameEn,
      product_name_ru: nameRu,
      description_uz: descriptionUz,
      description_en: descriptionEn,
      description_ru: descriptionRu,
      price: price,
      color: color,
      count: count,
      guarantee: garante,
      max_tension: max,
      weight: optimalPrice,
      size: size,
      sale: priceDis,
      all: link,
      three_months: three,
      six_months: six,
      tvelve_months: tweleve,
      image1: !uploadDatas.data
        ? FilterData.map((elem) => elem.image1)[0]
        : uploadDatas.data,
      image2: !uploadDatas1.data
        ? FilterData.map((elem) => elem.image2)[0]
        : uploadDatas1.data,
      image3: !uploadDatas2.data
        ? FilterData.map((elem) => elem.image3)[0]
        : uploadDatas2.data,
      category: category
        ? category
        : FilterData.map((elem) => elem.category.id)[0],
      sub_category: subCategory
        ? subCategory
        : FilterData.map((elem) => elem.sub_category.id)[0],
      brands: brand ? brand : FilterData.map((elem) => elem.brands.id)[0],
    };
    await dispatch(ProductPut({ body, id: id }));
    window.location.reload();
  };
  return (
    <>
      <DraverCommon
        title="Изменить продукт"
        open={openPut}
        onClose={handleClosePut}
      >
        <>
          <Wrapper onSubmit={HandelSubmit}>
            <div className="input_wrap">
              <div className="scrool">
                {Data.map((elem) =>
                  elem.id == id ? (
                    <>
                      <Row className="row">
                        <Col lg={4}>
                          <h4>Добавить первое изображение</h4>
                          <Row className="row">
                            <Col className="col_upload" lg={6}>
                              {uploadDatas.Loading == true ? (
                                <div className="spinss">
                                  <Spin indicator={antIcon} />
                                </div>
                              ) : uploadDatas.Success == true ? (
                                <Image
                                  width="100%"
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    zIndex: "99999999",
                                    objectFit: "cover",
                                  }}
                                  src={uploadDatas.data}
                                />
                              ) : (
                                <Image
                                  width="100%"
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    zIndex: "99999999",
                                    objectFit: "cover",
                                  }}
                                  src={elem.image1}
                                />
                              )}
                            </Col>
                            <Col className="col_upload" lg={6}>
                              {uploadDatas.Loading == true ? (
                                <div className="spins">
                                  <Spin indicator={antIcon} />
                                </div>
                              ) : (
                                <>
                                  <input
                                    type="file"
                                    id="file"
                                    onChange={HandleChange}
                                  />
                                  <label for="file" class="custom-file-upload">
                                    <span className="span-download">
                                      <ion-icon name="cloud-download-outline"></ion-icon>
                                      <span>Загрузить фото</span>
                                    </span>
                                  </label>
                                </>
                              )}
                            </Col>
                          </Row>
                        </Col>
                        <Col lg={4}>
                          <h4>Добавить второе изображение</h4>
                          <Row className="row">
                            <Col className="col_upload" lg={6}>
                              {uploadDatas1.Loading == true ? (
                                <div className="spinss">
                                  <Spin indicator={antIcon} />
                                </div>
                              ) : uploadDatas1.Success == true ? (
                                <Image
                                  width="100%"
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    zIndex: "99999999",
                                    objectFit: "cover",
                                  }}
                                  src={uploadDatas1.data}
                                />
                              ) : (
                                <Image
                                  width="100%"
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    zIndex: "99999999",
                                    objectFit: "cover",
                                  }}
                                  src={elem.image2}
                                />
                              )}
                            </Col>
                            <Col className="col_upload" lg={6}>
                              {uploadDatas1.Loading == true ? (
                                <div className="spins">
                                  <Spin indicator={antIcon} />
                                </div>
                              ) : (
                                <>
                                  <input
                                    type="file"
                                    id="file1"
                                    onChange={HandleChange}
                                  />
                                  <label for="file1" class="custom-file-upload">
                                    <span className="span-download">
                                      <ion-icon name="cloud-download-outline"></ion-icon>
                                      <span>Загрузить фото</span>
                                    </span>
                                  </label>
                                </>
                              )}
                            </Col>
                          </Row>
                        </Col>
                        <Col lg={4}>
                          <h4>Добавить третье изображение</h4>
                          <Row className="row">
                            <Col className="col_upload" lg={6}>
                              {uploadDatas2.Loading == true ? (
                                <div className="spinss">
                                  <Spin indicator={antIcon} />
                                </div>
                              ) : uploadDatas2.Success == true ? (
                                <Image
                                  width="100%"
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    zIndex: "99999999",
                                    objectFit: "cover",
                                  }}
                                  src={uploadDatas2.data}
                                />
                              ) : (
                                <Image
                                  width="100%"
                                  height="100%"
                                  style={{
                                    aspectRatio: "1 / 1",
                                    borderRadius: "20px",
                                    zIndex: "99999999",
                                    objectFit: "cover",
                                  }}
                                  src={elem.image3}
                                />
                              )}
                            </Col>
                            <Col className="col_upload" lg={6}>
                              {uploadDatas2.Loading == true ? (
                                <div className="spins">
                                  <Spin indicator={antIcon} />
                                </div>
                              ) : (
                                <>
                                  <input
                                    type="file"
                                    id="file2"
                                    onChange={HandleChange}
                                  />
                                  <label for="file2" class="custom-file-upload">
                                    <span className="span-download">
                                      <ion-icon name="cloud-download-outline"></ion-icon>
                                      <span>Загрузить фото</span>
                                    </span>
                                  </label>
                                </>
                              )}
                            </Col>
                          </Row>
                        </Col>
                        <Col lg={12}>
                          <div className="infor_box">
                            <p>
                              <span>Формат: </span>PNG, JPEG, JPG, SVG.
                              Рекомендуемое разрешение <span>1080×1080</span>
                            </p>
                            <p>
                              <span>Размер: </span>размер файла не должен
                              превышать 5 MB
                            </p>
                          </div>
                        </Col>
                        <Col lg={12}>
                          <h2>Название продукта</h2>
                        </Col>
                        <Col lg={4}>
                          <h4>На русском</h4>
                          <InputCommon
                            defaultValue={elem.product_name_ru}
                            onChange={(e) => setNameRu(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>На английском</h4>
                          <InputCommon
                            defaultValue={elem.product_name_en}
                            onChange={(e) => setNameEn(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>На узбекском</h4>
                          <InputCommon
                            defaultValue={elem.product_name_uz}
                            onChange={(e) => setNameUz(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={12}>
                          <h2>Описание продукта</h2>
                        </Col>
                        <Col lg={4}>
                          <h4>На русском</h4>
                          <TextArea
                            onChange={(e) =>
                              setDescriptionRu(e.currentTarget.value)
                            }
                            autoSize={{ minRows: 12, maxRows: 14 }}
                            defaultValue={elem.description_ru}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>На английском</h4>
                          <TextArea
                            defaultValue={elem.description_en}
                            onChange={(e) =>
                              setDescriptionEn(e.currentTarget.value)
                            }
                            autoSize={{ minRows: 12, maxRows: 14 }}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>На узбекском</h4>
                          <TextArea
                            defaultValue={elem.description_uz}
                            onChange={(e) =>
                              setDescriptionUz(e.currentTarget.value)
                            }
                            autoSize={{ minRows: 12, maxRows: 14 }}
                          />
                        </Col>
                        <Col lg={12}>
                          <h2>Другие дополнения к продукту</h2>
                        </Col>
                        <Col lg={4}>
                          <h4>Цена</h4>
                          <InputCommon
                            defaultValue={elem.price}
                            onChange={(e) => setPrice(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Оптимальные цена</h4>
                          <InputCommon
                            defaultValue={elem.weight}
                            onChange={(e) =>
                              setOptimalPrice(e.currentTarget.value)
                            }
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Цвет</h4>
                          <InputCommon
                            defaultValue={elem.color}
                            onChange={(e) => setColor(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Количество</h4>
                          <InputCommon
                            defaultValue={elem.count}
                            onChange={(e) => setCount(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Гарантия в годах</h4>
                          <InputCommon
                            defaultValue={elem.guarantee}
                            onChange={(e) => setGarante(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Максимальное напряжение</h4>
                          <InputCommon
                            defaultValue={elem.max_tension}
                            onChange={(e) => setMax(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Размер</h4>
                          <InputCommon
                            defaultValue={elem.size}
                            onChange={(e) => setSize(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Цена без скидки</h4>
                          <InputCommon
                            defaultValue={elem.sale}
                            onChange={(e) => setPriceDis(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>PDF-ссылка</h4>
                          <InputCommon
                            defaultValue={elem.all}
                            onChange={(e) => setLink(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={12}>
                          <h2>Ежемесячные цены</h2>
                        </Col>
                        <Col lg={4}>
                          <h4>3 месяца</h4>
                          <InputCommon
                            defaultValue={elem.three_months}
                            onChange={(e) => setThree(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>6 месяца</h4>
                          <InputCommon
                            defaultValue={elem.six_months}
                            onChange={(e) => setSix(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>12 месяца</h4>
                          <InputCommon
                            defaultValue={elem.tvelve_months}
                            onChange={(e) => setTweleve(e.currentTarget.value)}
                          />
                        </Col>
                        <Col lg={12}>
                          <h2>Выбор продукта</h2>
                        </Col>
                        <Col lg={4}>
                          <h4>Категория</h4>
                          <SelectCommon
                            defaultValue={elem.category.id}
                            options={CategoryOptions}
                            placeholder={"Выберите категорию"}
                            onChange={HandelCategoryChange}
                          />
                        </Col>
                        <Col lg={4}>
                          <h4>Подкатегория</h4>
                          <SelectCommon
                            defaultValue={elem.sub_category.id}
                            options={SubCategoryOptions}
                            onChange={HandelSubCategoryChange}
                            placeholder={"Выберите подкатегорию"}
                          />
                          <p>
                            Чтобы выбрать подкатегорию, сначала вам нужно
                            выбрать саму категорию.
                          </p>
                        </Col>
                        <Col lg={4}>
                          <h4>Бренд</h4>
                          <SelectCommon
                            defaultValue={elem.brands.id}
                            onChange={HandelBrandChange}
                            options={BrandOptions}
                            placeholder={"Выберите бренд"}
                          />
                        </Col>
                      </Row>
                    </>
                  ) : null
                )}
                <hr />
                <CommonBtn
                  type="submit"
                  style={{
                    margin: "20px 10px 0 auto",
                    padding: "15px 40px",
                    border: "2px solid #f3f3f3",
                    borderRadius: "50px",
                  }}
                >
                  Изменить
                </CommonBtn>
              </div>
            </div>
          </Wrapper>
        </>
      </DraverCommon>
    </>
  );
}
export default ProductsPut;
