import React, { useEffect, useState } from "react";
import ProductsTable from "./table";
import { useDispatch } from "react-redux";
import {
  ProductDelete,
  ProductsGet,
  ProductsGetId,
} from "../../redux/products";
import PostProduct from "./post";
import { CategoriesGet } from "../../redux/category";
import { BrandsGet } from "../../redux/brands";
import ProductPut from "./put";
import Cookies from "universal-cookie";
const cookies = new Cookies();

function ProductsComponent({ open, handleClose }) {
  const dispatch = useDispatch();

  const [productId, setProductId] = useState();

  useEffect(() => {
    dispatch(ProductsGet());
    dispatch(CategoriesGet());
    dispatch(BrandsGet());
  }, []);

  const [openPut, setOpenPut] = useState(false);

  const onClosePut = () => {
    setOpenPut(false);
  };

  const handlePutModal = async (e) => {
    setOpenPut(true);
    setProductId(e.currentTarget.id);
  };

  const HandleDelete = async (e) => {
    await dispatch(ProductDelete(e.currentTarget.id));
    dispatch(ProductsGet());
  };
  return (
    <>
      <ProductsTable
        handlePutModal={handlePutModal}
        HandleDelete={HandleDelete}
      />
      <PostProduct open={open} handleClose={handleClose} />
      <ProductPut
        openPut={openPut}
        handleClosePut={onClosePut}
        put_id={productId}
      />
    </>
  );
}

export default ProductsComponent;
