import React, { useEffect, useMemo, useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import ModalCommon from "../../common/Modal/Modal";
import { Wrapper } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import {
  ProductPost,
  ProductUpload,
  ProductUpload1,
  ProductUpload2,
} from "../../../redux/products/index";
import { Row, Col } from "react-grid-system";
import SelectCommon from "../../common/select/index";
import DrawerCommon from "../../common/Drawer/index";
import InputCommon from "../../common/input/index";
import { Spin, Input, Image } from "antd";
import { LoadingOutlined } from "@ant-design/icons";
import "./styles.css";
import TextArea from "antd/es/input/TextArea";

function PostProduct({ open, handleClose }) {
  const dispatch = useDispatch();
  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );

  // Upload
  const HandleChange = (e) => {
    dispatch(ProductUpload(e));
  };
  const HandleChange1 = (e) => {
    dispatch(ProductUpload1(e));
  };
  const HandleChange2 = (e) => {
    dispatch(ProductUpload2(e));
  };

  const uploadDatas = useSelector((state) => state.product.productUpload);
  const uploadDatas1 = useSelector((state) => state.product.productUpload1);
  const uploadDatas2 = useSelector((state) => state.product.productUpload2);

  // states

  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const [nameUz, setNameUz] = useState();

  const [descriptionRu, setDescriptionRu] = useState();
  const [descriptionEn, setDescriptionEn] = useState();
  const [descriptionUz, setDescriptionUz] = useState();

  const [price, setPrice] = useState();
  const [optimalPrice, setOptimalPrice] = useState();
  const [color, setColor] = useState();

  const [count, setCount] = useState();
  const [garante, setGarante] = useState();
  const [max, setMax] = useState();

  const [size, setSize] = useState();
  const [priceDis, setPriceDis] = useState();
  const [link, setLink] = useState();

  const [three, setThree] = useState();
  const [six, setSix] = useState();
  const [tweleve, setTweleve] = useState();

  const [category, setCategory] = useState();
  const [subCategory, setSubCategory] = useState();
  const [brand, setBrand] = useState();
  const [disabled, setDisabled] = useState(true);

  // select options

  const CategoryOptions = [];
  const CategoryDatas = useSelector(
    (state) => state.category.categoriesGet.data
  );
  CategoryDatas?.map((elem) =>
    CategoryOptions.push({
      label: elem.category_ru,
      value: elem.id,
    })
  );

  const HandelCategoryChange = (e) => {
    setCategory(e);
    setDisabled(false);
  };

  // sub

  const SubCategoryOptions = [];
  if (category) {
    CategoryDatas.map((elem) =>
      elem.id == category
        ? elem.sub_category.map((items) =>
            SubCategoryOptions.push({
              label: items.title_ru,
              value: items.id,
            })
          )
        : null
    );
  }

  const HandelSubCategoryChange = (e) => {
    setSubCategory(e);
  };

  const BrandOptions = [];
  const BrandDatas = useSelector((state) => state.brand.brandsGet.data);
  BrandDatas.map((elem) =>
    BrandOptions.push({
      label: elem.brands_name_ru,
      value: elem.id,
    })
  );

  const HandelBrandChange = (e) => {
    setBrand(e);
  };

  // post function

  const HandelSubmit = async (e) => {
    e.preventDefault();
    const body = {
      product_name_uz: nameUz,
      product_name_en: nameEn,
      product_name_ru: nameRu,
      description_uz: descriptionUz,
      description_en: descriptionEn,
      description_ru: descriptionRu,
      price: price,
      color: color,
      count: count,
      guarantee: garante,
      max_tension: max,
      weight: optimalPrice,
      size: size,
      sale: priceDis,
      all: link,
      three_months: three,
      six_months: six,
      tvelve_months: tweleve,
      image1: uploadDatas.data,
      image2: uploadDatas1.data,
      image3: uploadDatas2.data,
      category: category,
      sub_category: subCategory,
      brands: brand,
    };
    await dispatch(ProductPost(body));
    window.location.reload();
  };

  return (
    <DrawerCommon title="Добавить услугу" open={open} onClose={handleClose}>
      <>
        <Wrapper onSubmit={HandelSubmit}>
          <div className="input_wrap">
            <div className="scrool">
              <Row className="row">
                <Col lg={4}>
                  <h4>Добавить первое изображение</h4>
                  <Row className="row">
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spinss">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : uploadDatas.Success == true ? (
                        <Image
                          style={{
                            aspectRatio: "1 / 1",
                            borderRadius: "20px",
                            zIndex: "99999999",
                            objectFit: "cover",
                          }}
                          src={uploadDatas.data}
                        />
                      ) : (
                        <div className="none_img">
                          <i class="bx bxs-image"></i>
                        </div>
                      )}
                    </Col>
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spins">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : (
                        <>
                          <input
                            type="file"
                            id="file"
                            onChange={HandleChange}
                          />
                          <label for="file" class="custom-file-upload">
                            <span className="span-download">
                              <ion-icon name="cloud-download-outline"></ion-icon>
                              <span>Загрузить фото</span>
                            </span>
                          </label>
                        </>
                      )}
                    </Col>
                  </Row>
                </Col>
                <Col lg={4}>
                  <h4>Добавить второе изображение</h4>
                  <Row className="row">
                    <Col className="col_upload" lg={6}>
                      {uploadDatas1.Loading == true ? (
                        <div className="spinss">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : uploadDatas1.Success == true ? (
                        <Image
                          style={{
                            aspectRatio: "1 / 1",
                            borderRadius: "20px",
                            zIndex: "99999999",
                            objectFit: "cover",
                          }}
                          src={uploadDatas1.data}
                        />
                      ) : (
                        <div className="none_img">
                          <i class="bx bxs-image"></i>
                        </div>
                      )}
                    </Col>
                    <Col className="col_upload" lg={6}>
                      {uploadDatas1.Loading == true ? (
                        <div className="spins">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : (
                        <>
                          <input
                            type="file"
                            id="file1"
                            onChange={HandleChange1}
                          />
                          <label for="file1" class="custom-file-upload">
                            <span className="span-download">
                              <ion-icon name="cloud-download-outline"></ion-icon>
                              <span>Загрузить фото</span>
                            </span>
                          </label>
                        </>
                      )}
                    </Col>
                  </Row>
                </Col>
                <Col lg={4}>
                  <h4>Добавить третье изображение</h4>
                  <Row className="row">
                    <Col className="col_upload" lg={6}>
                      {uploadDatas2.Loading == true ? (
                        <div className="spinss">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : uploadDatas2.Success == true ? (
                        <Image
                          style={{
                            aspectRatio: "1 / 1",
                            borderRadius: "20px",
                            zIndex: "99999999",
                            objectFit: "cover",
                          }}
                          src={uploadDatas2.data}
                        />
                      ) : (
                        <div className="none_img">
                          <i class="bx bxs-image"></i>
                        </div>
                      )}
                    </Col>
                    <Col className="col_upload" lg={6}>
                      {uploadDatas2.Loading == true ? (
                        <div className="spins">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : (
                        <>
                          <input
                            type="file"
                            id="file2"
                            onChange={HandleChange2}
                          />
                          <label for="file2" class="custom-file-upload">
                            <span className="span-download">
                              <ion-icon name="cloud-download-outline"></ion-icon>
                              <span>Загрузить фото</span>
                            </span>
                          </label>
                        </>
                      )}
                    </Col>
                  </Row>
                </Col>
                <Col lg={12}>
                  <div className="infor_box">
                    <p>
                      <span>Формат: </span>PNG, JPEG, JPG, SVG. Рекомендуемое
                      разрешение <span>1080×1080</span>
                    </p>
                    <p>
                      <span>Размер: </span>размер файла не должен превышать 5 MB
                    </p>
                  </div>
                </Col>
                <Col lg={12}>
                  <h2>Название продукта</h2>
                </Col>
                <Col lg={4}>
                  <h4>На русском</h4>
                  <InputCommon
                    onChange={(e) => setNameRu(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>На английском</h4>
                  <InputCommon
                    onChange={(e) => setNameEn(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>На узбекском</h4>
                  <InputCommon
                    onChange={(e) => setNameUz(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={12}>
                  <h2>Описание продукта</h2>
                </Col>
                <Col lg={4}>
                  <h4>На русском</h4>
                  <TextArea
                    onChange={(e) => setDescriptionRu(e.currentTarget.value)}
                    autoSize={{ minRows: 12, maxRows: 14 }}
                  />
                </Col>
                <Col lg={4}>
                  <h4>На английском</h4>
                  <TextArea
                    onChange={(e) => setDescriptionEn(e.currentTarget.value)}
                    autoSize={{ minRows: 12, maxRows: 14 }}
                  />
                </Col>
                <Col lg={4}>
                  <h4>На узбекском</h4>
                  <TextArea
                    onChange={(e) => setDescriptionUz(e.currentTarget.value)}
                    autoSize={{ minRows: 12, maxRows: 14 }}
                  />
                </Col>
                <Col lg={12}>
                  <h2>Другие дополнения к продукту</h2>
                </Col>
                <Col lg={4}>
                  <h4>Цена</h4>
                  <InputCommon
                    onChange={(e) => setPrice(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Оптимальные цена</h4>
                  <InputCommon
                    onChange={(e) => setOptimalPrice(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Цвет</h4>
                  <InputCommon
                    onChange={(e) => setColor(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Количество</h4>
                  <InputCommon
                    onChange={(e) => setCount(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Гарантия в годах</h4>
                  <InputCommon
                    onChange={(e) => setGarante(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Максимальное напряжение</h4>
                  <InputCommon
                    onChange={(e) => setMax(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Размер</h4>
                  <InputCommon
                    onChange={(e) => setSize(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Цена без скидки</h4>
                  <InputCommon
                    onChange={(e) => setPriceDis(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>PDF-ссылка</h4>
                  <InputCommon
                    onChange={(e) => setLink(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={12}>
                  <h2>Ежемесячные цены</h2>
                </Col>
                <Col lg={4}>
                  <h4>3 месяца</h4>
                  <InputCommon
                    onChange={(e) => setThree(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>6 месяца</h4>
                  <InputCommon
                    onChange={(e) => setSix(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={4}>
                  <h4>12 месяца</h4>
                  <InputCommon
                    onChange={(e) => setTweleve(e.currentTarget.value)}
                  />
                </Col>
                <Col lg={12}>
                  <h2>Выбор продукта</h2>
                </Col>
                <Col lg={4}>
                  <h4>Категория</h4>
                  <SelectCommon
                    options={CategoryOptions}
                    placeholder={"Выберите категорию"}
                    onChange={HandelCategoryChange}
                  />
                </Col>
                <Col lg={4}>
                  <h4>Подкатегория</h4>
                  <SelectCommon
                    options={SubCategoryOptions}
                    disabled={disabled}
                    onChange={HandelSubCategoryChange}
                    placeholder={"Выберите подкатегорию"}
                  />
                  <p>
                    Чтобы выбрать подкатегорию, сначала вам нужно выбрать саму
                    категорию.
                  </p>
                </Col>
                <Col lg={4}>
                  <h4>Бренд</h4>
                  <SelectCommon
                    onChange={HandelBrandChange}
                    options={BrandOptions}
                    placeholder={"Выберите бренд"}
                  />
                </Col>
              </Row>
              <CommonBtn
                type="submit"
                style={{
                  margin: "20px 10px 0 auto",
                  padding: "15px 40px",
                  border: "2px solid #f3f3f3",
                  borderRadius: "50px",
                }}
              >
                Добавить
              </CommonBtn>
            </div>
          </div>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default PostProduct;
