import * as React from "react";
import { useSelector, useDispatch } from "react-redux";
import { Image, Popover, Tooltip, Space } from "antd";
import TableCommon from "../../common/table";
import { useEffect } from "react";
import "./styles.css";
import { ProductsGet } from "../../../redux/products";
import Cokies from "universal-cookie";

function ProductsTable({ HandleDelete, handlePutModal, selectId }) {
  const productGetState = useSelector((state) => state.product);

  const rows = productGetState.productsGet?.data;

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(ProductsGet());
  }, []);

  const data = [];
  rows.map((elem, index) => {
    data.push({
      key: elem.id,
      Имявкаталоге: (
        <span style={{ fontWeight: "bold" }}>{elem?.category?.title_ru}</span>
      ),
      data: index + 1,
      Фото: (
        <Image
          width={60}
          height="100%"
          style={{
            aspectRatio: "1 / 1",
            borderRadius: "20px",
            objectFit: "cover",
          }}
          src={elem.image1}
        />
      ),
      название: (
        <Tooltip
          width="100%"
          color="#0C1019"
          title={
            <div className="tooltip_box">
              <span>
                <span>{elem.product_name_ru}</span>
              </span>
            </div>
          }
        >
          <span className="curson">{elem.product_name_ru}</span>
        </Tooltip>
      ),
      Цена: (
        <Tooltip
          color="#0C1019"
          title={
            <div className="tooltip_box">
              <span>
                <span>{elem.price}</span>
              </span>
            </div>
          }
        >
          <span className="curson">{elem.price}</span>
        </Tooltip>
      ),
      Бренд: (
        <Tooltip
          color="#0C1019"
          title={
            <div className="tooltip_box">
              <span>
                <span>{elem.brands.brands_name_ru}</span>
              </span>
            </div>
          }
        >
          <span className="curson">{elem.brands.brands_name_ru}</span>
        </Tooltip>
      ),
      Категория: (
        <Tooltip
          color="#0C1019"
          title={
            <div className="tooltip_box">
              <span>
                <span>{elem.category.category_ru}</span>
              </span>
            </div>
          }
        >
          <span className="curson">{elem.category.category_ru}</span>
        </Tooltip>
      ),
      Подкатегория: (
        <Tooltip
          color="#0C1019"
          title={
            <div className="tooltip_box">
              <span>
                <span>{elem.sub_category.title_ru}</span>
              </span>
            </div>
          }
        >
          <span className="curson">{elem.sub_category.title_ru}</span>
        </Tooltip>
      ),

      Действие: (
        <div className="boxx">
          <div className="btn-wraps">
            <button onClick={handlePutModal} id={elem.id}>
              <i id={elem.id} class="bx bx-message-square-edit"></i>
            </button>
          </div>

          <Space wrap>
            <Popover
              trigger="click"
              placement="rightBottom"
              content={
                <div className="content_delete_box">
                  <p>Вы уверены, что хотите удалить эту услугу?</p>
                  <p>
                    При удалений услугу вся информация принадлежащая <br />{" "}
                    данной услуги будут удалены безвозратно
                  </p>
                  <div className="btn_wrap_delete">
                    <button
                      onClick={HandleDelete}
                      id={elem.id}
                      className="yes_btn"
                    >
                      Удалить
                    </button>
                  </div>
                </div>
              }
              title={
                <div className="delete_box">
                  <i class="bx bxs-error-circle"></i>
                  <span>Удалите услугу</span>
                </div>
              }
            >
              <div className="btn-wrap">
                <button id={elem.id}>
                  <i class="bx bxs-trash"></i>
                </button>
              </div>
            </Popover>
          </Space>
        </div>
      ),
    });
  });

  const columns = [
    {
      title: "№",
      dataIndex: "data",
      key: "data",
      fixed: "left",
      align: "center",
      width: "50px",
    },
    {
      title: "Фото",
      dataIndex: "Фото",
      key: "Фото",
      align: "center",
      width: "120px",
    },
    {
      title: "название",
      dataIndex: "название",
      key: "название",
      align: "center",
      width: "240px",
    },
    {
      title: "Цена",
      dataIndex: "Цена",
      key: "Цена",
      align: "center",
    },
    {
      title: "Бренд",
      dataIndex: "Бренд",
      key: "Бренд",
      align: "center",
    },
    {
      title: "Категория",
      dataIndex: "Категория",
      key: "Категория",
      align: "center",
    },
    {
      title: "Подкатегория",
      dataIndex: "Подкатегория",
      key: "Подкатегория",
      align: "center",
      width: "150px",
    },
    {
      title: "Действие",
      dataIndex: "Действие",
      key: "Действие",
      fixed: "right",
      align: "center",
      width: "200px",
    },
  ];

  return (
    <>
      <TableCommon
        bordered
        columns={columns}
        data={data}
        pagination={false}
        scroll={{
          y: 425,
        }}
      />
    </>
  );
}

export default ProductsTable;
