import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import ProductsComponent from "../../components/products";
// import { AdminGet } from "../../redux/admin";
function Products() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    // dispatch(AdminGet());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  console.log(window.location.pathname);

  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon
          title={"Продукты"}
          onClick={handleOpen}
          textBtn={"Добавить продукт"}
        />
        <ProductsComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon
          title={"Продукты"}
          onClick={handleOpen}
          textBtn={"Добавить продукт"}
        />

        <ProductsComponent handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default Products;
